/* Main Button Area css
============================================================================================ */
.submit_btn {
	width: auto;
	display: inline-block;
	background: $white_color;
	padding: 0px 50px;
	color: #fff;
	font-size: 13px;
	font-weight: 500;
	line-height: 50px;
	border-radius: 5px;
	outline: none !important;
	box-shadow: none !important;
	text-align: center;
	border: 1px solid $border_color;
	cursor: pointer;
	@include transform_time(0.5s);
	&:hover {
		background: transparent;
	}
}
.btn_1 {
	display: inline-block;
	padding: 9.5px 28px;
	border-radius: 50px;
	background-color: $white_color;
	font-size: 14px;
	font-weight: 400;
	color: $common_color;
	@include transform_time(0.5s);
	text-transform: capitalize;
	&:hover {
		color: $common_color;
		background-color: #f17896;
	}
	@media #{$small_mobile} {
