/*=================== custom button rule start ====================*/

.button {
	display: inline-block;
	border: 1px solid transparent;
	font-size: 15px;
	font-weight: 500;
	padding: 12px 54px;
	border-radius: 4px;
	color: $white_color;
	border: 1px solid #f17896;
	text-transform: uppercase;
	background-color: #f17896;
	cursor: pointer;
	@include transform_time(0.5s);

	@media (max-width: 767px) {
		font-size: 13px;
		padding: 9px 24px;
	}

	&:hover {
		color: $white_color;
	}

	&-link {
		letter-spacing: 0;
		color: #3b1d82;
		border: 0;
		padding: 0;

		&:hover {
			background: transparent;
			color: #3b1d82;
		}
	}

	&-header {
		color: $white_color;
		border-color: $border_color;

		&:hover {
			background: #b8024c;
			color: $white_color;
		}
	}

	&-contactForm {
		color: $white_color;
		border-color: $border_color;
		padding: 12px 25px;

		&:hover {
			// border-color: $title-color;
			// background: $title-color;
			// color: $white_color;
		}
	}
}

